.gallery__list {
  margin-top: 60px;
  margin-bottom: 60px;
  display: grid;

  @media screen and (min-width: 1280px) {
    grid-template-columns: repeat(3, 1fr);
    gap: 32px 16px;
  }
  @media screen and (min-width: 768px) and(max-width: 1279.5px) {
    grid-template-columns: repeat(2, 1fr);
    gap:32px;
  }
  @media screen and (max-width: 767.5px) {
    grid-template-columns: 1fr;
    gap: 20px;
  }
}
.gallery__item {
width: 100%;
padding-bottom: 3px;
cursor: pointer;
}
.gallery__item:hover {
box-shadow: 0 3px 2px #777;
}


.gallery__img {
  width: 100%;
  height: 574px;
  // display: block;
  border-radius: 5px;
  overflow: hidden;
  margin-bottom: 12px;
  @media screen and (min-width: 768px) and(max-width: 1279.5px) {
    height: 455px;
  }
  @media screen and (max-width: 767.5px) {
    height: calc((100vw - 40px) *1.5);
    min-height: 402px;
    max-height: 660px;
  }

}
.gallery__info {
  font-family: Roboto;
  letter-spacing: 0;
  font-style:normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 1.2;
  color: #000000;
  padding-left: 3px;
  text-shadow: 0px 4px 4px 0px #00000040, 0px 4px 4px 0px #00000040;

  @media screen and (min-width:768px) and (max-width:1279.5) {
    font-size: 12px;
    line-height: 1.33;
  }
  @media screen and (min-width:480px)  and (max-width: 767.5px) {
    line-height: 1.17;
  }
  @media screen and (max-width: 479.5px) {
    font-size: 4vw;
  }
}
.gallery__title {
text-transform: uppercase;
}
.gallery__text {
  color:var(--text-color-light-orange);
  @media screen and (max-width: 767.5px) {
    line-height: 1.33;
  }
}
.page-up {
  width: 66px;
  height: 86px;
  border-color: transparent;
  border-radius: 15px;
  position: fixed;
  bottom: 10;
  right: 10;
  cursor: pointer;
  opacity: 0.4;
  transition: opacity 2s;
  background-image: url('../images/UP1.png');

  background-clip: padding-box;
  background-repeat: no-repeat;
}
.page-up:hover {
  opacity: 0.8;

}
.is-hidden {
  display: none;
}